const slugify = (str) => {
    str = str.replace(/^\s+|\s+$/g, ''); // xa khong trng  u v cui chui
    str = str.toLowerCase(); // chuyn i thnh ch thng
    str = str
        .replace(/[^a-z0-9 -]/g, '') // xa k t khng phi ch ci, s, du cch v du gch ngang
        .replace(/\s+/g, '-') // thay th du cch bng du gch ngang
        .replace(/-+/g, '-'); // loi b cc du gch ngang lin tip
    return str;
};

const formattedDateTime = (now, char = '', format = '') => {
    const date = now.getDate();
    const month = now.getMonth() + 1;
    const year = now.getFullYear();
    const hours = now.getHours();
    const minutes = now.getMinutes();

    // nh dng li chui ngy/thng/nm v gi/pht
    const formattedDate = ('0' + date).slice(-2); // nh dng ngy vi 2 ch s, vd: 01, 02, ..., 31
    const formattedMonth = ('0' + month).slice(-2); // nh dng thng vi 2 ch s, vd: 01, 02, ..., 12
    const formattedYear = year.toString().slice(-2); // nh dng nm vi 2 ch s, vd: 21, 22, ..., 99
    const formattedHours = ('0' + hours).slice(-2); // nh dng gi vi 2 ch s, vd: 00, 01, ..., 23
    const formattedMinutes = ('0' + minutes).slice(-2); // nh dng pht vi 2 ch s, vd: 00, 01, ..., 59

    // To chui kt qu theo nh dng "dd/mm/yy hh:mm"
    if (format === '') {
        return `${formattedDate}${char}${formattedMonth}${char}${formattedYear}`;
    } else {
        return `${formattedDate}${char}${formattedMonth}${char}${formattedYear} ${formattedHours}:${formattedMinutes}`;
    }
};

export { slugify, formattedDateTime };
